/* ==========   ==========  */
body {
    font-family: $font-main;
    background-color: $brand-main-color;
    color: $text-simple-dark-color;
}

body.modal-open {
    overflow: hidden;
}

h1,
h2,
h3,
p {
    margin: 0;
    padding: 0;
}

img {
    display: block;
    max-width: 100%;
    height: auto;
}

/*    ul */
ul,
ol {
    margin: 0;
    padding: 0;
    list-style: none;
}

/*    <a> */
a {
    text-decoration: none;
}

/*     textarea */
textarea {
    resize: none;
}

button {
    cursor: pointer;

    //       
    box-sizing: border-box;
}

/* ========== /  ========== */