/* === BUTTONS === */
//        .
//       - (--)

//    (buy now)
.btn-filled {
  width: 152px;
  height: 42px;
  padding: 12px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.285714285714286;

  align-items: center;
  text-align: center;

  border-radius: 10px;
  background: $brand-second-color;
  color: $text-simple-dark-color;
  border: 0;

  transition: color $transition, background $transition;

  &:hover {
    background: $focus-color;
  }

  &:focus {
    background: $focus-color;
    color: $text-light-color;
  }

  &:disabled {
    background: $disabled-color;
  }
}

//   "Read more"
.btn-filled--middle {
  width: 164px;
}

//   "Our Locations"
.btn-filled--big {
  width: 196px;
}

//      $mobile
.btn-filled--thin {
  height: 32px;
  padding: 7px;

  font-size: 12px;
  line-height: 1.25;
}

//    (products)
.btn-border {
  width: 134px;
  height: 42px;
  padding: 12px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.285714285714286;

  align-items: center;
  text-align: center;

  background: transparent;
  color: $text-simple-dark-color;

  border: 2px solid $brand-second-color;
  border-radius: 10px;

  transition: border-color $transition, color $transition;

  &:hover {
    border-color: $focus-color;
  }

  &:focus {
    border-color: $focus-color;
    color: $text-light-color;
  }

  &:disabled {
    border-color: $disabled-color;
  }
}

//    Franchise
.btn-border--big {
  width: 196px;
}

//    Products  $tablet  $mobile
.btn-border--small {
  width: 62px;
  height: 24px;
  padding: 7px;

  border-radius: 8px;
}

//      ("How its made?"  $desctop)
.btn-underline {
  width: 152px;
  height: 42px;
  padding: 12px;

  font-weight: 600;
  font-size: 14px;
  line-height: 1.285714285714286;

  align-items: center;
  text-align: center;

  background: transparent;
  color: $brand-second-color;
  border: 0;

  transition: color $transition;

  &:hover {
    color: $focus-color;
  }

  &:focus {
    color: $text-light-color;
  }

  &:disabled {
    color: $disabled-color;
  }
}

//  "How its made?"  $tablet  $mobile
.btn-underline--small {
  width: 82px;
  height: 24px;
  padding: 7px;
}

